@import "base";

@keyframes apply {
  from {transform: scale(1.0) translate(calc(20% + 20px), calc(-15% - 30px));}
  to {transform: scale(1.1) translate(calc(20% + 20px), calc(-15% - 30px));}
}

@font-face {
	font-family: "PicNic";
	src: url('/fonts/PicNic-Regular.eot');
	src: url('/fonts/PicNic-Regular.eot') format('embedded-opentype'),
       url('/fonts/PicNic-Regular.woff2') format('woff2'),
       url('/fonts/PicNic-Regular.woff') format('woff'),
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}
@keyframes float2 {
  0%, 100% {
    transform: translate(-50%, 0);
  }
  50% {
    transform: translate(-50%, -10px);
  }
}

.cloud {
  animation: float 5s ease-in-out infinite alternate; /* Adjust duration and timing function as needed */
}


